import React from 'react';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Map } from 'react-lodash';
import SnoozeIcon from '@material-ui/icons/Snooze';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import moment from 'moment';
import { teal } from '@material-ui/core/colors';
import { UseMutationResult } from 'react-query';

const optionLabels: {[key: number]: string} = {
  1: '1 hour',
  2: '2 hours',
  4: '4 hours',
  24: '24 hours'
}
const options = [1, 2, 4, 24];

export default function NotificationEventsGroup(properties: { eventsList:NotificationEvent[], handleClickEvents: () => void, sensorMutation: UseMutationResult<any, unknown, Sensor, unknown>, sensor: Sensor }) {
  const { 
    eventsList, 
    handleClickEvents, 
    sensorMutation, 
    sensor 
  } = properties;
  const snoozedColor = teal["400"];

  const epochToTime = (epoch: number) => {
    const d = new Date(0);
    d.setUTCSeconds(epoch / 1000);
    return d.toLocaleTimeString();
  }

  const valueProp = 1;
  const [value, setValue] = React.useState<number>(valueProp);
  const radioGroupRef = React.useRef<React.RefObject<HTMLInputElement> | null>(null);
  const [openSnooze, setOpenSnooze] = React.useState(false);

  React.useEffect(() => {
    if (!openSnooze) {
      setValue(valueProp);
    }
  }, [valueProp, openSnooze]);

  const handleSnooze = () => {
    setOpenSnooze(true);
  }

  const handleEntering = () => {
    if (radioGroupRef.current != null) {
      console.log('handleEntering', typeof radioGroupRef.current);
      // radioGroupRef.current.focus();
    }
  };

  const handleCancel = () => {
    setOpenSnooze(false);
  };

  const handleOk = () => {
    setOpenSnooze(false);
    sensorMutation.mutate({ sensorId: eventsList[0].sensorId, snoozeUntil: new Date().getTime() + (1000 * value * 60 * 60), isFavourite: null });
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue(parseInt(event.target.value));
  };

  const now = new Date().getTime();

  const snoozeButtonColor = sensor.snoozeUntil || 0 > now
    ? snoozedColor
    : 'black';

  const firstSensorValue = eventsList[0];
  const latestSensorValue = [...eventsList].reverse()[0];

  const getSnoozeUntil = (sensor: Sensor): number => {
    return sensor.snoozeUntil || 0;
  }

  return (
    <React.Fragment>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="content"
          id="header"
        >
          <Grid container spacing={0} alignItems="center">
            <Grid item xs={11}>
              <ListItemText primary={firstSensorValue.name} secondary={epochToTime(latestSensorValue.timestamp)} />
            </Grid>
            <Grid item xs={1}>
              <SnoozeIcon
                style={{ color: snoozeButtonColor }}
                onClick={(event) => { event.stopPropagation(); handleSnooze(); }}
                onFocus={(event) => event.stopPropagation()}
              />
            </Grid>
          </Grid>
        </AccordionSummary>
        <AccordionDetails>
          <List>
            <Map collection={eventsList} iteratee={(entry: NotificationEvent) =>
              <ListItem button onClick={handleClickEvents} key={`${entry.sensorId}-${entry.timestamp}`}>
                <ListItemText secondary={epochToTime(entry.timestamp)} primary={entry.message} />
              </ListItem>
            } />
          </List>
        </AccordionDetails>
      </Accordion>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        maxWidth="xs"
        onEntering={handleEntering}
        aria-labelledby="confirmation-dialog-title"
        open={openSnooze}
      >
        <DialogTitle id="confirmation-dialog-title">Snooze For</DialogTitle>
        <DialogContent dividers>
          {/* {sensor.snoozeUntil || 0 > now && `Active again ${moment((sensor.snoozeUntil || 0)).fromNow()}.`} */}
          {getSnoozeUntil(sensor) > now && `Active again ${moment(getSnoozeUntil(sensor)).fromNow()}.`}
          <RadioGroup
            ref={radioGroupRef}
            aria-label="snooze-for"
            name="snooze-for"
            value={value}
            onChange={handleChange}
          >
            {options.map((option) => (
              <FormControlLabel value={option} key={option} control={<Radio />} label={optionLabels[option]} />
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel} color="primary">
            Cancel
        </Button>
          <Button onClick={handleOk} color="primary">
            Ok
        </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}