import React from 'react';
import Grid from '@material-ui/core/Grid';
import ValueButton from './ValueButton';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { Map } from 'react-lodash';
import ClearIcon from '@material-ui/icons/Clear';
import Skeleton from '@material-ui/lab/Skeleton';
import { useQuery, useMutation, useQueryClient, UseMutationResult } from 'react-query';
import { loadSensors, updateSensor } from './service/sensors';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 440,
    marginTop: 10,
    marginBottom: 10
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    fontSize: 16,
    fontWeight: 700,
    position: 'relative'
  },
  clearIcon: {
    color: '#DDDDDD',
    position: 'absolute',
    right: 2,
    top: 2,
    '&:hover': {
      color: '#444444',
    },
  },
  sensorName: {
    marginTop: 0
  }
}));

const skeleton = () => {
  return (
    <>
      <Grid item xs={6} key={1}>
        <Skeleton variant="rect" width={166} height={128} />
      </Grid>
      <Grid item xs={6} key={2}>
        <Skeleton variant="rect" width={166} height={128} />
      </Grid>
    </>
  );
};

interface classes {
  root: string,
  paper: string,
  clearIcon: string,
  sensorName: string
}

const contents = (classes: classes, favouriteSensors: string[], sensorData: SensorData, mutation: UseMutationResult<any, unknown, Sensor, unknown>) => {
  return (
    <Map collection={favouriteSensors} iteratee={(sensorId: string) =>
      <Grid item xs={6} key={sensorId}>
        <Paper className={classes.paper}>
          <ClearIcon className={classes.clearIcon} onClick={() => mutation.mutate({ sensorId, isFavourite: false, snoozeUntil: null })} />
          <p className={classes.sensorName}>
            {sensorData.items[sensorId] && sensorData.items[sensorId][0].name}
          </p>
          <div>
            <Map collection={sensorData.items[sensorId]} iteratee={(sensor:SensorWithValue) =>
              ['temp', 'humidity'].includes(sensor.type) ? <ValueButton sensor={sensor} key={sensor.type} /> : ''
            } />
          </div>
        </Paper>
      </Grid>
    } />
  );
};

export default function SensorsFavourites(properties: { sensorData: SensorData, isLoading: boolean }) {
  const { sensorData, isLoading } = properties;
  const queryClient = useQueryClient();

  const { data: sensors = [] } = useQuery('loadSensors', loadSensors);
  const mutation = useMutation(updateSensor, {
    onSuccess: () => {
      queryClient.invalidateQueries('loadSensors')
    },
  });

  const [favouriteSensors, setFavouriteSensors] = React.useState([]);

  React.useEffect(() => {
    setFavouriteSensors(
      sensors.filter((sensor: Sensor) => sensor.isFavourite)
        .map((sensor: Sensor) => sensor.sensorId)
    );
  }, [sensors]);

  const classes = useStyles();

  return (
    <Grid id="favourites" container spacing={3} className={classes.root}>
      <Grid item xs={12}>
        <Grid container spacing={1}>
          {isLoading ? skeleton() : contents(classes, favouriteSensors, sensorData, mutation)}
        </Grid>
      </Grid>
    </Grid>
  );
}
