import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import List from '@material-ui/core/List';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { loadSensorValues } from './service/sensorValues'
import { useQuery } from 'react-query';
import Skeleton from '@material-ui/lab/Skeleton';

export default function SensorValueListDialog(properties: { sensor: SensorWithValue, open: boolean, onClose: () => void }) {
  const {
    sensor,
    open,
    onClose
  } = properties;

  const [valueResolution, setValueResolution] = React.useState('15minutes');
  const [currentSensor, setCurrentSensor] = React.useState(sensor);
  const [enabled, setEnabled] = React.useState(false);
  const [interval, setInterval] = React.useState({from: '6', to: ''});

  const queryKeyValues = { sensorId: currentSensor.sensorid, valueResolution, interval };
  const queryOptions = { enabled, cacheTime: 1000 * 5 * 60, staleTime: 1000 * 5 * 60 };

  const { 
    data: newSensorResult = null, 
    isLoading 
  } = useQuery(['loadCurrentSensorValues', queryKeyValues], loadSensorValues, queryOptions);

  React.useEffect(() => {
    if (newSensorResult !== null) {
      const sensors = newSensorResult['items'][currentSensor.sensorid];
      const newSensor = sensors.filter((s: SensorWithValue) => s.type === currentSensor.type)[0];
      setCurrentSensor(newSensor);
    }
  }, [currentSensor, newSensorResult]);

  const handleChange = (event: any) => {
    const date = new Date();

    switch (event.target.value as string) {
      case 'day': {
        const fromDate = new Date(new Date().setMonth(date.getMonth()-1));
        setInterval({from: fromDate.toLocaleDateString('sv-se'), to: date.toLocaleDateString('sv-se')});
        break;
      }
      case 'hour': {
        const fromDate = new Date(new Date().setDate(date.getDate()-3));
        setInterval({from: fromDate.toLocaleDateString('sv-se') + '-00', to: date.toLocaleDateString('sv-se') + '-23'});
        break;
      }
      default: {
        setInterval({from: '6', to: ''});
        break;
      }
    };
    setValueResolution(event.target.value);
    setEnabled(true);
  };

  // const epochToDateTime = (epoch) => {
  //   const d = new Date(0);
  //   d.setUTCSeconds(epoch / 1000);
  //   return d.toLocaleDateString('sv-SE') + ' ' + d.toLocaleTimeString();
  // }

  // const epochToDate = (epoch: number) => {
  //   const d = new Date(0);
  //   d.setUTCSeconds(epoch / 1000);
  //   return d.toLocaleDateString('sv-SE');
  // }

  const epochToTime = (epoch: number) => {
    const d = new Date(0);
    d.setUTCSeconds(epoch / 1000);
    return d.toLocaleTimeString();
  }

  const stringFormatOptions = { minimumFractionDigits: 2, maximumFractionDigits: 2 };

  return (
    <Dialog onClose={onClose} aria-labelledby="simple-dialog-title" open={open}>
      <DialogTitle id="simple-dialog-title">{currentSensor.name + ' (' + currentSensor.type + ')'}</DialogTitle>
      <FormControl>
        <InputLabel id="demo-simple-select-label">Resolution</InputLabel>
        <Select
          labelId="value-resolution-label"
          id="value-resolution-select"
          value={valueResolution}
          onChange={handleChange}
        >
          <MenuItem value={'15minutes'}>15 minutes</MenuItem>
          <MenuItem value={'hour'}>Hourly</MenuItem>
          <MenuItem value={'day'}>Daily</MenuItem>
        </Select>
      </FormControl>
      <List>
        {isLoading && 
        <Skeleton variant="rect" width={166} height={128} />
        }
        {!isLoading && currentSensor && currentSensor.data && currentSensor.data.map((entry, idx) => {
          if (valueResolution === 'day') {
            const secondary = (entry[2] && entry[3] && <>
              <b>Min: </b>{entry[2].toLocaleString(undefined, stringFormatOptions)}
              <br />
              <b>Max: </b>{entry[3].toLocaleString(undefined, stringFormatOptions)}
            </>);
            console.log('day', currentSensor);
            return (
              <ListItem button onClick={() => {setValueResolution('hour'); setInterval({from: entry[0] + '-00', to: entry[0] + '-23'});}} key={idx}>
                <ListItemText primary={entry[0]} secondary={secondary}/>&nbsp;
                <ListItemText primary={entry[1].toLocaleString(undefined, stringFormatOptions)} />
              </ListItem>
            );
          } else if (valueResolution === 'hour') {
            const secondary = (entry[2] && entry[3] && <>
              <b>Min: </b>{entry[2].toLocaleString(undefined, stringFormatOptions)}
              <br />
              <b>Max: </b>{entry[3].toLocaleString(undefined, stringFormatOptions)}
            </>);
            return (
              <ListItem button onClick={onClose} key={idx}>
                <ListItemText primary={entry[0]} secondary={secondary}/>&nbsp;
                <ListItemText primary={entry[1].toLocaleString(undefined, stringFormatOptions)} />
              </ListItem>
            );
          }
          return (
            <ListItem button onClick={onClose} key={idx}>
              <ListItemText primary={epochToTime(entry[0])} />
              <ListItemText primary={entry[1].toLocaleString(undefined, stringFormatOptions)} />
            </ListItem>
          );
        })}
      </List>
    </Dialog>
  )
}