import React from 'react';
import moment from 'moment';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Chunk, Map } from 'react-lodash';
import { makeStyles } from '@material-ui/core/styles';
import ValueButton from './ValueButton'

const useStyles = makeStyles(() => ({
  root: {
    paddingTop: 0,
    paddingBottom: 0,
  },
}));

export default function SensorItem(properties: {sensors: SensorWithValue[]}) {
  const classes = useStyles();
  const { sensors } = properties;

  return (
    <ListItem className={classes.root}>
      <ListItemText
        primary={sensors[0].name.substring(0, 35) + (sensors[0].name.length > 35 ? "..." : "")}
        secondary={moment(sensors[0].data.slice().reverse()[0][0]).fromNow()} />

      <div style={{ display: 'flex', flexDirection: 'column' }}>
        <Chunk array={sensors} size={2}>
          {(chunks: SensorWithValue[]) =>
            chunks.map((chunk, idx) =>
              <div key={idx}>
                <Map collection={chunk} iteratee={(sensor: SensorWithValue) =>
                  <ValueButton sensor={sensor} key={sensor.type} />
                } />
              </div>
            )
          }
        </Chunk>
      </div>
    </ListItem>
  )
}
